.wrapper {
  display: grid;
  /* background-color: blue; */
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  grid-template-rows: 158px 327px 359px 359px 359px;
}

.attention {
  display: grid;
  margin-left: 20px;
  margin-right: 20px;
}

.titleOne {
}

.titleOne span {
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;
  color: #000000;
}

.description {
}

.description span {
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
}

.send {
  display: grid;
  background-color: #f2f2f2;
  border-radius: 30px;
  grid-template-rows: 48px 1fr;
  gap: 20px;
  margin-top: 47px;
}

.sendTitle {
  margin-left: 20px;
  margin-top: 10px;
}

.sendTitle span {
  font-style: normal;
  font-weight: 700;
  font-size: 35px;
  line-height: 48px;
  text-align: center;
  color: #000000;
}

.sendBlock {
  display: grid;
  grid-template-rows: 30px 1fr;
  gap: 10px;
  margin-top: 35px;
}

.rateInfo {
  display: grid;
  grid-template-columns: 2fr 1fr;
}

.rateInfo span {
  margin-left: 20px;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 30px;
  color: #000000;
}

.inputBlock {
  display: grid;
  grid-template-columns: 430px 270px;
  justify-content: space-between;
  margin: 0px 20px 0 20px;
  height: 64px;
}

.inputBlock input {
  height: 50px;
  border: 1px solid #d9d9d9;
  background: white;
  font-size: 25px;
  outline: none;
}

.inputBlock select {
  height: 50px;
  border: 1px solid #d9d9d9;
  background: white;
  font-size: 25px;
  outline: none;
}

.get {
  display: grid;
  background-color: #f2f2f2;
  border-radius: 30px;
  margin-top: 40px;
}

.getTitle {
  margin-left: 20px;
  margin-top: 10px;
}

.getTitle span {
  font-style: normal;
  font-weight: 700;
  font-size: 35px;
  line-height: 48px;
  text-align: center;
  color: #000000;
}

.wallet {
  display: grid;
  grid-template-rows: 30px 1fr;
  margin-left: 20px;
}

.wallet span {
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
}

.wallet input {
  margin-top: 10px;
  height: 45px;
  border: 1px solid #d9d9d9;
  background: white;
  font-size: 25px;
  width: 700px;
  outline: none;
}

.personInfo {
  display: grid;
  grid-template-rows: 60px 1fr;
  background-color: #f2f2f2;
  margin-top: 40px;
  border-radius: 30px;
  grid-template-rows: 60px 1fr 100px;
}

.emailBlock {
  display: grid;
  grid-template-rows: 30px 1fr;
  margin-left: 20px;
  margin-top: 30px;
}

.emailBlock span {
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
}

.emailBlock input {
  margin-top: 10px;
  height: 45px;
  border: 1px solid #d9d9d9;
  background: white;
  font-size: 25px;
  width: 400px;
  outline: none;
}

.button {
  margin-left: 20px;
}

.button button {
  height: 60px;
  width: 250px;
  background: #d9d9d9;
  border-radius: 10px;
  border: 0;
  text-align: center;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
  cursor: pointer;
}

.button :hover {
  background: #b7b7b7;
}

.change ol {
  margin-top: 35px;
}

.change li {
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: #000000;
  margin-left: 20px;
  margin-right: 20px;
}
